import Component from "vue-class-component";
import axios from "axios";
import * as indexApi from "../../api/indexApi";

@Component({
  props: {},
  components: {
    // categoryView,
  }
})
export default class AddGoods extends Vue {
  userList = [];
  userForm = {};
  modelType = "add";
  model = false;
  changeModel(type, row) {
    this.modelType = type;
    this.userForm = {};
    if (row) {
      let a = {};
      Object.assign(a, row);
      this.userForm = a;
console.log(row)
      console.log(this.options)
this.options.forEach((item,index)=>{
  if( item.departmentId !==row.departmentId){
  }
  console.log(  item.departmentId ==row.departmentId)
})


    }
    this.model = true;
  }

  bianli(list,id){
    list.filter((item,index)=>{
    return  item.departmentId == id
    })

      return 
  }

  page = 0;
  pageSize = 10;
  total = 100;
  onPageChange(page) {
    this.page = page - 1;
    this.getUserList();
  }

  getUserList() {
    indexApi
      .getUserList({
        keyword: this.keyword,
        page: this.page,
        pageSize: this.pageSize
      })
      .then(res => {
        if (res["retCode"]) {
          this.userList = res.data.user;
          this.total = res.data.page.total;
        } else {
          if (!res["islogin"]) {
            this.$alert(res["message"]);
          }
          console.error("数据查询错误");
        }
      });
  }

  departmentModel = [];
  roleIdModel = "";
  changeDepartment(e) {
    this.roleIdModel = "";
    this.getRoleList(e[e.length - 1]);
  }
  roleList = [];
  getRoleList(departmentId) {
    indexApi
      .queryRoleList({
        departmentId: departmentId
      })
      .then(res => {
        if (res["retCode"]) {
          this.roleList = res.data.roleList;
        } else {
          if (!res["islogin"]) {
            this.$alert(res["message"]);
          }
          console.error("数据查询错误");
        }
      });
  }
  keyword = "";
  options = [];
  departmentId = "";
  getDepartment() {
    indexApi.getDepartment({}).then(res => {
      console.log(res);
      if (res["retCode"]) {
        this.options = res.data.children;
        // this.options.unshift({
        //   departmentId:"",
        //   label:"全部",
        //   name:"全部",
        //   value:''
        // })
      } else {
        if (!res["islogin"]) {
          this.$alert(res["message"]);
        }
        console.error("数据查询错误");
      }
    });
  }

  doSubmit() {





    if ((this.userForm["userName"] || "") == "") {
      this.$alert("请输入账号");
      return;
    }
    if ((this.userForm["name"] || "") == "") {
      this.$alert("请输入姓名");
      return;
    }

    if (this.modelType == "add") {
      if (
        !this.userForm["userPassword"] ||
        this.userForm["userPassword"].length < 6
      ) {
        this.$alert("密码必须6位字符以上");
        return;
      }
      if ((this.userForm["userPassword"] || "") == "") {
        this.$alert("请输入密码");
        return;
      }
      if (this.userForm["userPassword"] != this.userForm["re_user_password"]) {
        this.$alert("两次密码必须一致");
        return;
      }
    }


if(this.userForm['adminState']  == '0'){
    if (!this.departmentModel || this.departmentModel.length == 0) {
      this.$alert("请选择部门");
      return;
    }
    if ((this.roleIdModel || "") == "") {
      this.$alert("请选择职位");
      return;
    }
}

    this.userForm["departmentId"] = this.departmentModel[
      this.departmentModel.length - 1
    ];
    this.userForm["roleId"] = this.roleIdModel;
    this.userForm["userId"] = this.userForm["userID"];


    if (this.modelType == "add") {
      
      indexApi.addSystemUserModel(this.userForm).then(res => {
        console.log(res);
        if (res["retCode"]) {
          this.$message("添加成功");
          this.getUserList();
          this.model = false;
        } else {
          if (!res["islogin"]) {
            this.$alert(res["message"]);
          }
          console.error("数据查询错误");
        }
      });
    } else {
      indexApi.updateSystemUserModel(this.userForm).then(res => {
        if (res["retCode"]) {
          this.$message("添加成功");
          this.getUserList();
          this.model = false;
        } else {
          if (!res["islogin"]) {
            this.$alert(res["message"]);
          }
          console.error("数据查询错误");
        }
      });
    }
  }
doStop(index, row){

   if(row.adminState == '1'){
      this.$alert('不可停用系统管理员账号')
      return ;
    }
    let status = row.userStatus=='1' ?'停用':'启用'

    this.$confirm("是否"+status+"该用户?", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    })
      .then(() => {

         indexApi
          .updateSystemUserModel({
            userId: row.userID,
            userStatus: row.userStatus == '0'?'1':'0'
          })
          .then(res => {
            if (res["retCode"]) {
              this.$message("已停用");
              this.getUserList();
            } else {
              if (!res["islogin"]) {
                this.$alert(res["message"]);
              }
              console.error("数据查询错误");
            }
          });
          
      })
      .catch(() => {
        this.$message({
          type: "info",
          message: "已取消操作"
        });
      });
}
  doDelete(index, row) {

    if(row.adminState == '1'){
      this.$alert('不可删除系统管理员账号')
      return ;
    }
    this.$confirm("是否删除该用户?", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    })
      .then(() => {
        indexApi
          .deleteSystemUserModel({
            userId: row.userID
          })
          .then(res => {
            if (res["retCode"]) {
              this.$message("已删除");
              this.getUserList();
            } else {
              if (!res["islogin"]) {
                this.$alert(res["message"]);
              }
              console.error("数据查询错误");
            }
          });
      })
      .catch(() => {
        this.$message({
          type: "info",
          message: "已取消操作"
        });
      });
  }
  initPassword(row) {
    this.$confirm("是否对此用户执行重置密码?", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    })
      .then(() => {
        indexApi
          .updateSystemUserModel({
            userId: row.userID,
            userPassword: "123456"
          })
          .then(res => {
            if (res["retCode"]) {
              this.$message("已重置");
              this.getUserList();
            } else {
              if (!res["islogin"]) {
                this.$alert(res["message"]);
              }
              console.error("数据查询错误");
            }
          });
      })
      .catch(() => {
        this.$message({
          type: "info",
          message: "已取消操作"
        });
      });
  }

  Count = 0;

  allCount() {
    indexApi.allCountuser().then(res => {
      console.log(res);
      if (res["retCode"]) {
        this.Count = res.data.num;
      } else {
        if (!res["islogin"]) {
          this.$alert(res["message"]);
        }
        console.error("数据查询错误");
      }
    });
  }

  mounted() {
    this.allCount();
    this.getUserList();
    this.getDepartment();
  }
}



